import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { AuthService } from '../../../services/auth/auth.service';
import {DataService} from "../../../services/data.service";
import { HttpErrorResponse } from '@angular/common/http';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

@Component({
  selector: 'app-course-introduction',
  templateUrl: './course-introduction.component.html',
  styleUrls: ['./course-introduction.component.scss']
})
export class CourseIntroductionComponent {
	configurations: any;
	pageLoading = false;
	authUser: any;
	lesson: any;
	id: any;
	course: any;
	
	iframeVideo: SafeResourceUrl | undefined;
	
	constructor(
		private router: Router,  
		private authService: AuthService,
		private dataService: DataService,
		private route: ActivatedRoute,
		private sanitizer: DomSanitizer
	) {}
	
	getLessonIntro(key: any){
		this.pageLoading = true;
		this.authService.getLessonIntro(key).subscribe((response: any) => {
		  this.lesson = response;
		  this.iframeVideo =  this.sanitizer.bypassSecurityTrustResourceUrl(this.lesson.intro_video);
		  this.lesson.details = this.sanitizer.bypassSecurityTrustHtml(this.lesson.details);
		  this.lesson.intro_video = this.sanitizer.bypassSecurityTrustHtml(this.lesson.intro_video);
		  this.pageLoading = false;
		});
	}
	ngOnInit() {
		this.authService.authorize().then(result => {
			if (result) {
				this.authService.user.subscribe((response: any) => {
					this.authUser = response;
				});
			}
		});
		this.dataService.configurations.subscribe((configurations: any) => {
		  if(configurations.settings){
			this.configurations = configurations;
		  }
		});
		this.route.url.subscribe((url: any) => {
			this.id = '';
			if(url.length > 0){
				for(let part of url){
				  this.id += '/' + part.path.replace('.html', '')
				}
				this.id = this.id.slice(21);
			}
			this.getLessonIntro(this.id);
		});
	}
}
