import { Component } from '@angular/core';
import { AuthService } from '../../services/auth/auth.service';

@Component({
  selector: 'app-guide-nav',
  templateUrl: './guide-nav.component.html',
  styleUrls: ['./guide-nav.component.scss']
})
export class GuideNavComponent {
	authUser: any;
	is_partners = false;
	show_courses_module = false;
	constructor(
		private authService: AuthService
	) {}
	
	ngOnInit() {
		this.authService.authorize().then(result => {
			if (result) {
				this.authService.user.subscribe((response: any) => {
					this.authUser = response;
					this.authUser.fields.filter((item: any) => {
						console.log(item);
						if(item.field_key==='is_partner' && item.field_value==='1'){
							this.is_partners = true;
						}
						if(item.field_key==='show_courses_module' && item.field_value==='1'){
							this.show_courses_module = true;
						}
					  });
				});
				
			}
		});
		
	}
	logout(event:any) {
		this.authService.logout().subscribe(res => {
			window.location.href = '/login.html';
		});
	}

}
