import { FormGroup } from '@angular/forms';
import {formatDate} from '@angular/common';

// custom validator to check that two fields match
export function CheckDate(date: string, month: string, year: string) {
	return (formGroup: FormGroup) => {

		const dob = formGroup.controls[date].value + '/' + (parseInt(formGroup.controls[month].value)<=9?"0":'')+formGroup.controls[month].value + '/' + formGroup.controls[year].value;

    function isFutureDate(idate: any){
      var today = new Date().getTime(),
          idate = idate.split("/");
      idate = new Date(idate[2], idate[1] - 1, idate[0]).getTime();
      return (today - idate) < 0 ? true : false;
    }

    if(isFutureDate(dob)){
      formGroup.controls[year].setErrors({ futureDate: true });
    }
    else{
      formGroup.controls[year].setErrors(null);
    }
	}
}
