import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import {PageNotFoundComponent} from "./page-not-found/page-not-found.component";
import {DefaultComponent} from "./default/default.component";
import { BestemmingenComponent } from './bestemmingen/bestemmingen.component';
import { ContactComponent } from './contact/contact.component';
import { LoginComponent } from './login/login.component';
import { VakantiesComponent } from './vakanties/vakanties.component';
import { PageComponent } from './page/page.component';
import { BookingComponent } from './booking/booking.component';
import { ContactSingleComponent } from './contact/contact-single/contact-single.component';
import { BlogComponent } from './blog/blog.component';
import { BlogSingleComponent } from './blog/blog-single/blog-single.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ForumComponent } from './dashboard/forum/forum.component';
import { RecentActivityComponent } from './dashboard/forum/recent-activity/recent-activity.component';
import { ReactiesComponent } from './dashboard/forum/reacties/reacties.component';
import { InsurancesComponent } from './dashboard/insurances/insurances.component';
import { ProfileEditComponent } from './dashboard/profile-edit/profile-edit.component';
import { DashboardPrintComponent } from './dashboard/dashboard-print/dashboard-print.component';
import { BookingDetailsComponent } from './dashboard/booking-details/booking-details.component';
import { AuthGuard } from './guards/auth.guard';
import { GuestGuard } from './guards/guest.guard';
import { ForumNewComponent } from './dashboard/forum/forum-new/forum-new.component';
import { DashboardListComponent } from './dashboard/dashboard-list/dashboard-list.component';
import { GuideComponent } from './guide/guide.component';
import { GuideDashboardComponent } from './guide/guide-dashboard/guide-dashboard.component';
import { GuideChangePasswordComponent } from './guide/guide-change-password/guide-change-password.component';
import { TourGuidesComponent } from './guide/tour-guides/tour-guides.component';
import { CoursesComponent } from './guide/courses/courses.component';
import { TourGuideAddEditComponent } from './guide/tour-guides/tour-guide-add-edit/tour-guide-add-edit.component';
import { LessonsCourseIntroductionComponent } from './guide/courses/lessons-course-introduction/lessons-course-introduction.component';
import { CourseIntroductionComponent } from './guide/courses/course-introduction/course-introduction.component';
import { QuizesResultComponent } from './guide/courses/quizes-result/quizes-result.component';
import { TourGuideDetailsComponent } from './guide/tour-guide-details/tour-guide-details.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
import { SitemapComponent } from './sitemap/sitemap.component';
import { ShareImagesComponent } from './guide/share-images/share-images.component';
import { GuideTourInfoComponent } from './guide/guide-tour-info/guide-tour-info.component';

const routes: Routes = [
  {
    path: 'vakanties.html',
    component: VakantiesComponent,
  },
  {
    path: 'blog.html',
    component: BlogComponent,
  },
  {
    path: 'blog/c/:name\.html',
    component: BlogComponent,
  },
  {
    path: 'blog/:name\.html',
    component: BlogSingleComponent,
  },
  {
    path: 'bestemmingen.html',
    component: BestemmingenComponent,
  },
  {
    path: 'contact.html',
    component: ContactComponent,
  },
  {
    path: 'contact',
    component: ContactSingleComponent,
  },
  {
    path: 'support/:name\.html',
    component: ContactSingleComponent,
  },
  {
    path: 'support/:cname\/:name\.html',
    component: ContactSingleComponent,
  },
  {
    path: 'boek/:date/:name\.html',
    component: BookingComponent,
  },
  {
    path: 'boek/:date/:part/:name\.html',
    component: BookingComponent,
  },
  {
    path: 'reserveren/:date/:name\.html',
    component: BookingComponent,
    data: {reserve: true},
  },
  {
    path: 'reserveren/:date/:part/:name\.html',
    component: BookingComponent,
    data: {reserve: true},
  },
  {
    path: 'mijn/dashboard',
    component: DashboardComponent,
    canActivate: [AuthGuard],
    children:[
		{
			path: '',
			redirectTo: '/mijn/dashboard/list.html',
			pathMatch: 'full'
		},
		{
			path: 'list.html',
			component: DashboardListComponent,
		},
		{
			path: 'forum.html',
			component: ForumComponent,
			canActivate: [AuthGuard]
		},
		{
			path: 'forum/topic/:id\.html',
			component: ReactiesComponent,
			canActivate: [AuthGuard]
		},
		{
			path: 'forum/forum-new.html',
			component: ForumNewComponent,
			canActivate: [AuthGuard]
		},
		{
			path: 'forum/recent-activity.html',
			component: RecentActivityComponent,
			canActivate: [AuthGuard]
		},
		{
			path: 'insurances.html',
			component: InsurancesComponent,
		},
		{
			path: 'profile-edit.html',
			component: ProfileEditComponent,
			canActivate: [AuthGuard]
		},
		{
			path: 'dashboard-print.html',
			component: DashboardPrintComponent,
			canActivate: [AuthGuard]
		},
    ]
  }, 
  {
    path: 'mijn/dashboard/:id\.html',
    component: BookingDetailsComponent,
	canActivate: [AuthGuard]
  },
  {
    path: 'guide',
    component: GuideComponent,
    canActivate: [AuthGuard],
    children:[
		{
			path: '',
			redirectTo: '/guide/dashboard/list.html',
			pathMatch: 'full'
		},
		{
			path: 'dashboard/list.html',
			component: GuideDashboardComponent,
		},
		{
			path: 'password_edit.html',
			component: GuideChangePasswordComponent,
			canActivate: [AuthGuard]
		},
		{
			path: 'tourleader.html',
			component: TourGuidesComponent,
			canActivate: [AuthGuard]
		},
    {
			path: 'tourguideinfo.html',
			component: GuideTourInfoComponent,
			canActivate: [AuthGuard]
		},
		{
			path: 'tourleader_add.html',
			component: TourGuideAddEditComponent,
			canActivate: [AuthGuard]
		},
    {
			path: 'tourleader_edit/:id',
			component: TourGuideAddEditComponent,
			canActivate: [AuthGuard]
		},
		{
			path: 'courses.html',
			component: CoursesComponent,
			canActivate: [AuthGuard]
		},
		{
			path: 'course-lessons/:id\.html',
			component: LessonsCourseIntroductionComponent,
			canActivate: [AuthGuard]
		},
		{
			path: 'lesson-introduction/:id\.html',
			component: CourseIntroductionComponent,
			canActivate: [AuthGuard]
		},
		{
			path: 'course-quiz-result/:id\.html',
			component: QuizesResultComponent,
			canActivate: [AuthGuard]
		},
		{
			path: 'dashboard/:id\.html',
			component: TourGuideDetailsComponent,
			canActivate: [AuthGuard]
		},
    {
			path: 'share-images.html',
			component: ShareImagesComponent,
			canActivate: [AuthGuard]
		},
    ]
  },
  {
    path: 'login.html',
    component: LoginComponent,
	
  },
  {
    path: 'forgot-password.html',
    component: ForgotPasswordComponent,
	
  },
  {
    path: 'page',
    component: PageComponent,
  },
  {
    path: 'sitemap.html',
    component: SitemapComponent,
  },
  {
    path: '**',
    component: DefaultComponent,
    data: {defaultRoute: true},
  },
  
];

@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    RouterModule.forRoot(routes, { onSameUrlNavigation: 'reload', anchorScrolling: 'enabled', initialNavigation: 'enabledBlocking' })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
