import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { AuthService } from '../../../services/auth/auth.service';
import {DataService} from "../../../services/data.service";
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-lessons-course-introduction',
  templateUrl: './lessons-course-introduction.component.html',
  styleUrls: ['./lessons-course-introduction.component.scss']
})
export class LessonsCourseIntroductionComponent {
	configurations: any;
	pageLoading = false;
	authUser: any;
	lessons: any = [];
	id: any;
	course: any;
	
	constructor(
		private router: Router,  
		private authService: AuthService,
		private dataService: DataService,
		private route: ActivatedRoute,
	) {}
	
	getLessons(key: any){
		this.pageLoading = true;
		this.authService.getLessons(key).subscribe((response: any) => {
		  this.lessons = response.lessons.data;
		  this.course = response.course;
		  this.pageLoading = false;
		});
	}
	ngOnInit() {
		this.authService.authorize().then(result => {
			if (result) {
				this.authService.user.subscribe((response: any) => {
					this.authUser = response;
				});
			}
		});
		this.dataService.configurations.subscribe((configurations: any) => {
		  if(configurations.settings){
			this.configurations = configurations;
		  }
		});
		this.route.url.subscribe((url: any) => {
			this.id = '';
			if(url.length > 0){
				for(let part of url){
				  this.id += '/' + part.path.replace('.html', '')
				}
				this.id = this.id.slice(16);
			}
			this.getLessons(this.id);
		});
	}

}
