import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { ModalService } from '../../../services/modal.service';
import { AuthService } from '../../../services/auth/auth.service';
import {DataService} from "../../../services/data.service";
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-reacties',
  templateUrl: './reacties.component.html',
  styleUrls: ['./reacties.component.scss']
})
export class ReactiesComponent {
	configurations: any;
	pageLoading = false;
	authUser: any;
	forumTopic: any;
	id: any;
	commentForm:any;
	comment: any;
	isSubmitted = false;
	message:any;
	resData:any;
	
	constructor(
		private modalService: ModalService, 
		private router: Router,  
		private authService: AuthService,
		private dataService: DataService,
		private route: ActivatedRoute,
		private fb: UntypedFormBuilder
	) {
		this.commentForm = this.fb.group({
			topic_id: [''],
			content: ['', Validators.required]
		});
	}
	
	getTopicData(key: any){
		this.pageLoading = true;
		this.authService.getForumTopic(key).subscribe((response: any) => {
			this.forumTopic = response;
			this.commentForm.get('topic_id').setValue(this.forumTopic.id);
			this.pageLoading = false;
		});
	}
	ngOnInit() {
		
		this.dataService.configurations.subscribe((configurations: any) => {
		  if(configurations.settings){
			this.configurations = configurations;
		  }
		});
		this.route.url.subscribe((url: any) => {
			this.id = '';
			if(url.length > 0){
			for(let part of url){
			  this.id += '/' + part.path.replace('.html', '')
			}
			this.id = this.id.slice(13);
				console.log(this.id);
			}
			this.getTopicData(this.id);
		});
	}
	submitComment(event: any){
		event.preventDefault();
		this.isSubmitted = true;
		if (!this.commentForm.valid) return;
		this.comment = this.commentForm.value;
		if(this.comment.topic_id){
			this.authService.storeComment(this.comment).subscribe((response: any) => {
				this.isSubmitted = false;
				this.message = response.message;
				this.resData = response.reply;
				this.forumTopic.forum_replies.push(this.resData);
			});
		}
		
	}
}
