import { Component } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { BlogService } from '../services/blog.service';
import {DataService} from "../services/data.service";

@Component({
  selector: 'app-blog',
  templateUrl: './blog.component.html',
  styleUrls: ['./blog.component.scss']
})
export class BlogComponent {

	configurations: any;
	blogs: any;
	pageNumber = 0;
	categories: any;
	page:any = null;
	related_blogs: any;
	path = '';
	pageLoading = false;
	meta_title: any = {};
	meta_description: any = {};
	meta_keywords: any = {};

	constructor(
		private blogService: BlogService,
		private route: ActivatedRoute, 
		private router: Router,
		private dataService: DataService,
    ) {}

	ngOnInit(){
		
		this.dataService.configurationsSubject.subscribe((configurations: any) => {
			if(configurations.settings){
			  this.configurations = configurations;
			//   this.meta_title = this.configurations.config.blog_meta_title.value;
			//   this.meta_description = this.configurations.config.blog_meta_description.value;
			//   this.meta_keywords = this.configurations.config.blog_meta_keywords.value;
			  this.route.url.subscribe((url: any) => {
				this.path = '';
				if(url.length > 0){
				  for(let part of url){
					this.path += '/' + part.path.replace('.html', '')
				  }
				  this.path = this.path.slice(8);
				}
				this.getBlogData('', 1, this.path);
			  });
			  console.log(this.meta_title);
			}
		});
		
		
		
	}
	getRec(ch: any){
		console.log(ch);
	}
	getBlogData(search: any, pageNumber: number, seoUrl: string){
		if(!this.pageLoading){
		  this.pageLoading = true;
		  this.pageNumber += pageNumber;
		  this.blogService.getBlog({search}, this.pageNumber, seoUrl).subscribe((response: any) => {
			this.page = response.page;
			this.blogs = response.blogs.data;
			this.categories = response.categories;
			this.related_blogs = response.related_blogs;
			console.log(this.meta_title);
			this.dataService.setMeta({
				"title": this.page.meta_title,
				"description": this.page.meta_description,
				"keywords": this.page.meta_keywords,
				"date": ""
			});
			this.pageLoading = false;
		  });
		}
	}
}
