import {
  Component,
  ElementRef,
  Renderer2,
  ViewChild,
  Input,
  EventEmitter,
  Output,
} from '@angular/core';
import { FormControl } from '@angular/forms';

declare const $: any;

@Component({
  selector: 'app-date-range-picker',
  templateUrl: './date-range-picker.component.html',
  styleUrls: ['./date-range-picker.component.scss']
})
export class DateRangePickerComponent {

  date_input = new FormControl();
  @ViewChild('dateRange', { read: ElementRef }) dateRange!: ElementRef;
  @Input() label: string | undefined;
  @Input()  dates!: string;
  @Output() datesChange = new EventEmitter<string>();

  constructor(private renderer: Renderer2) {
    
  }

  ngOnInit() {
    console.log(this.label);
    if(typeof $ !== 'undefined'){
      $(window).on("load", () => {
        return;
      });
    }
  }

  ngAfterViewInit() {
    if(this.dateRange.nativeElement && typeof $ !== 'undefined') {
      setTimeout(() => {
        if(this.dateRange.nativeElement) {
          const options = {
            autoUpdateInput: false,
            locale: {
              format: 'DD/MM/YY'
            }
          };
      
          $(this.dateRange.nativeElement).daterangepicker(options);
          $(this.dateRange.nativeElement).on('apply.daterangepicker', (ev: any, picker: any) => {
            const str = picker.startDate.format("DD/MM/YY") + " - " + picker.endDate.format("DD/MM/YY");
            console.log(this.dateRange.nativeElement);
            $(this.dateRange.nativeElement).val(str);
            this.datesChange.emit(str);
          });
          $(this.dateRange.nativeElement).on('cancel.daterangepicker', (ev: any, picker: any) => {
            $(this.dateRange.nativeElement).val('');
            this.datesChange.emit('');
          });
        }
      }, 500);
    }
  }
}
