import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { HttpErrorResponse } from '@angular/common/http';
import { ModalService } from '../../services/modal.service';
import { AuthService } from '../../services/auth/auth.service';
import {DataService} from "../../services/data.service";


@Component({
  selector: 'app-tour-guides',
  templateUrl: './tour-guides.component.html',
  styleUrls: ['./tour-guides.component.scss']
})
export class TourGuidesComponent {
	tourGuidTripList = true;
	addEditTourGuidTrip = false;
	configurations: any;
	pageLoading = false;
	authUser: any;
	guides:any = [];
	trips:any = [];
	route_id;
	form: any;
	errors: any = {};
	processing;
	constructor(
		private modalService: ModalService, 
		private router: Router,  
		private authService: AuthService,
		private dataService: DataService,
		private route: ActivatedRoute,
		private fb: UntypedFormBuilder,
	) {
		this.route_id = 0;
		this.form = this.fb.group({
			id: [''],
			user_id: [''],
			trip_id: [0, Validators.required],
		});
		
		this.processing = false;
	}
	
	addTrip(id: any){
		this.tourGuidTripList = false;
		this.addEditTourGuidTrip = true;
		this.form.get('id').setValue(0);
		this.form.get('trip_id').setValue(0);
		this.form.get('user_id').setValue(id);
	}
	editTrip(trip: any){
		this.tourGuidTripList = false;
		this.addEditTourGuidTrip = true;
		this.form.get('id').setValue(trip.id);
		this.form.get('user_id').setValue(trip.user_id);
		this.form.get('trip_id').setValue(trip.trip_id);
	}
	listTrip(){
		this.tourGuidTripList = true;
		this.addEditTourGuidTrip = false;
	}

	openModal(id: string) {
		this.modalService.open(id);
	}
	closeModal(id: string) {
	  this.modalService.close(id);
	}
	getMyGuides(){
		if(!this.pageLoading){
		  this.pageLoading = true;
		  this.authService.getMyGuides().subscribe((response: any) => {
			this.guides = response.guides;
			this.pageLoading = false;
			this.dataService.setMeta({
			  "title": "",
			  "description": "",
			  "keywords": "",
			  "date": ''
			});
		  });
		  this.authService.getGuideTrips().subscribe((response: any) => {
			this.trips = response.user.trips;
			this.pageLoading = false;
		  });
		}
	}
	getGuideTrips(){
		if(!this.pageLoading){
		  this.pageLoading = true;
		  
		}
	}
	ngOnInit() {
		this.authService.authorize().then(result => {
			if (result) {
				this.authService.user.subscribe((response: any) => {
					this.authUser = response;
				})
			}
		});
		this.dataService.configurations.subscribe((configurations: any) => {
		  if(configurations.settings){
			this.configurations = configurations;
			this.getMyGuides();
			
		  }
		});
	}
	
	submitForm(event: any) {
		event.preventDefault();
		if (!this.form.valid) return;
		this.processing = true;
		const trip = this.form.value;
		if (this.route_id) trip.id = this.route_id;
		this.authService.updateGuideTrip(trip).subscribe((res: any) => {
			if(res.status){
				this.processing = false;
				this.addEditTourGuidTrip = false;
				this.tourGuidTripList = false;
				window.location.reload();
				this.router.navigate(['/guide/tourleader.html']);
			}
		},
		(errorResponse: HttpErrorResponse) => {
			//this.errors = this.responseErrorComponent.responseError(errorResponse, this.form);
			this.processing = false;
		});
	}
}