import { Inject, Injectable } from '@angular/core';
import {BehaviorSubject, Observable} from "rxjs";
import {HttpClient, HttpParams} from "@angular/common/http";
import {environment} from "../../environments/environment";
import {Meta, Title} from "@angular/platform-browser";
import { DOCUMENT } from '@angular/common';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  configurationsSubject = new BehaviorSubject<any>({});
  activeAgeGroupIndex = new BehaviorSubject<any>(-1);
  homePageParams = new BehaviorSubject<any>({});
  homePage = new BehaviorSubject<any>(false);
  public configurations = this.configurationsSubject.asObservable();
  private link: HTMLLinkElement | undefined;
  
	constructor(private http:HttpClient,
              private titleService: Title,
              private metaTagService: Meta,
              @Inject(DOCUMENT) private doc: any) { }

	getSettings(){
		this.http.get(environment.apiUrl + '/general-configurations').subscribe(res => {
			this.configurationsSubject.next(<any>res);
		 });
	}
  getMonths(attributeId: any): Observable<any>{
    return this.http.get(environment.apiUrl + '/months' + (attributeId ? '?attribute_id=' + attributeId : ''));
  }
  setHomePage(status: boolean){
    this.homePage.next(status);
  }
  setHomePageParams(params: any){
    this.homePageParams.next(params);
  }
  getHomePageData(attrbiuteId: any, month: any, params: any): Observable<any>{
    let queryParams = new HttpParams();
    if(attrbiuteId)
      queryParams = queryParams.append("attrbiuteId", attrbiuteId);
    if(month)
      queryParams = queryParams.append("month", month);
    if(params && params.date){
      queryParams = queryParams.append("date", params.date);
    }
    return this.http.get(environment.apiUrl + '/home', {params: queryParams});
  }

  getPageData(id: any): Observable<any>{
    let queryParams = new HttpParams();
    return this.http.get(environment.apiUrl + '/page/' + id, {params: queryParams});
  }

  getTripData(id: any, date: string, reservation_id: any, key: any): Observable<any>{
    let queryParams = new HttpParams();
    queryParams = queryParams.append("date", date);
	if(reservation_id)
      queryParams = queryParams.append("reservation_id", reservation_id);
	if(key)
      queryParams = queryParams.append("key", key);
    return this.http.get(environment.apiUrl + '/trip/' + id, {params: queryParams});
  }
  storeReminderForm(reminder: any, tripId: any): Observable<any>{
    return this.http.post(environment.apiUrl + '/save-reminder', reminder);
  }

  getCsrf(): Observable<any>{
    return this.http.get(environment.apiUrl + "/sanctum/csrf-cookie");
  }

  setMeta(meta: any){
	  this.titleService.setTitle(meta.title);
    this.metaTagService.removeTag("name=keywords");
    this.metaTagService.removeTag("name=meta_title");
    this.metaTagService.removeTag("name=meta_description");
    this.metaTagService.removeTag("name=date");
	  this.metaTagService.addTags([
      {
        name: 'keywords',
        content: meta.keywords ?? '',
      },
      {
        name: 'meta_title',
        content: meta.title ?? '',
      },
      {
        name: 'meta_description',
        content: meta.description ?? '',
      },
      { name: 'date', content: meta.date, scheme: 'YYYY-MM-DD' }
    ]);
    if (this.link === undefined) {
      this.link = this.doc.createElement('link');
      console.log(this.link);
      if(this.link){
        this.link.setAttribute('rel', 'canonical');
        this.doc.head.appendChild(this.link);
      }
    }
    if(this.link){
      let str = this.doc.URL.split('?')[0];
      str = str.endsWith('/') ? str.slice(0, -1) : str;
      this.link.setAttribute('href', str);
    }
  }

  checkRedirectUrl(url: string): Observable<any>{
    let queryParams = new HttpParams();
    queryParams = queryParams.append("url", url);
    return this.http.get(environment.apiUrl + "/check-redirect-url", {params: queryParams});
  }
}
