import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  Input,
  NgZone,
  Output,
  Renderer2,
  ViewChild,
  EventEmitter,
  SimpleChanges,
  ViewChildren,
  QueryList,
  AfterViewInit,
  OnChanges
} from '@angular/core';

declare const $: any;

@Component({
  selector: 'app-selectric',
  template: `
    <div #el class="form_control {{ class }}">
      <select>
          <option *ngFor="let item of options; let i = index" [value]="item.key">{{ item.value }}</option>
      </select>
    </div>
  `,
  changeDetection: ChangeDetectionStrategy.Default
})
export class SelectricComponent implements AfterViewInit, OnChanges {

  constructor(private renderer: Renderer2, private ngZone: NgZone) {}

  @Input() class: string | undefined;

  @Input() modelId!: any;
  @Input() options!: any;
  @Output() modelIdChange = new EventEmitter<any>();

  @ViewChildren('el') el: QueryList<any> | undefined;

  ngOnChanges(changes: SimpleChanges) {
    if(this.el && typeof $ !== 'undefined') {
      setTimeout(() => {
        if(this.el) {
          $(this.el.first.nativeElement).find("select").val(this.modelId);
          $(this.el.first.nativeElement).find("select").selectric('refresh');
        }
      },100);
    }
  }
  ngAfterViewInit() {
    if(this.el && typeof $ !== 'undefined') {
      // this.el.changes.subscribe((result: any) => {
      //   $(result.first.nativeElement).find("select").val(this.modelId);
      //   $(result.first.nativeElement).find("select").selectric('init');
      // });
      $(this.el.first.nativeElement).find("select").on("change", () => {
        if(this.el){
          this.modelId = $(this.el.first.nativeElement).find("select").val();
        }
        this.modelIdChange.emit(this.modelId);
      })
      $(this.el.first.nativeElement).find("select").val(this.modelId);
      $(this.el.first.nativeElement).find("select").selectric('init');
    }
    /*if(this.el){
      $(this.el.nativeElement).find("select").on("change", () => {
        if(this.el){
          this.modelId = $(this.el.nativeElement).find("select").val();
        }
        this.modelIdChange.emit(this.modelId);
      })
      console.log($(this.el.nativeElement).find("select").length);
      $(this.el.nativeElement).find("select").selectric();
    }
    $(window).on("load", () => {
      
    });*/
  }

}
