import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { ModalService } from '../../services/modal.service';
import { AuthService } from '../../services/auth/auth.service';
import {DataService} from "../../services/data.service";
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-share-images',
  templateUrl: './share-images.component.html',
  styleUrls: ['./share-images.component.scss']
})
export class ShareImagesComponent {

  configurations: any;
	pageLoading = false;
	authUser: any;
	trips:any = [];
	form: any;
	errors: any = {};
	processing;
	dropbox = false;
	constructor(
		private modalService: ModalService, 
		private router: Router,  
		private authService: AuthService,
		private dataService: DataService,
		private fb: UntypedFormBuilder,
	) {
		this.form = this.fb.group({
			id: [''],
			user_id: [''],
			trip_id: [0, Validators.required],
		});
		
		this.processing = false;
	}
	
	previousTrips(){
		if(!this.pageLoading){
		  this.pageLoading = true;
		  this.authService.previousTrips().subscribe((response: any) => {
			this.trips = response.user.trips;
			console.log(response.user.dropbox);
			if(response.user.dropbox != null){
				this.dropbox = true;
			}
			this.pageLoading = false;
			this.dataService.setMeta({
			  "title": "",
			  "description": "",
			  "keywords": "",
			  "date": ''
			});
		  });
		}
	}
	ngOnInit() {
		this.authService.authorize().then(result => {
			if (result) {
				this.authService.user.subscribe((response: any) => {
					this.authUser = response;
					this.form.get('user_id').setValue(this.authUser.id);
				})
			}
		});
		this.dataService.configurations.subscribe((configurations: any) => {
		  if(configurations.settings){
			this.configurations = configurations;
			this.previousTrips();
		  }
		});
	}
	submitForm(event: any, trip_id: any) {
		event.preventDefault();
		this.form.get('trip_id').setValue(trip_id);
		if (!this.form.valid) return;
		this.processing = true;
		const dropbox = this.form.value;
		this.authService.updateDropbox(dropbox).subscribe((res: any) => {
			if(res.status){
				this.processing = false;
				this.dropbox = true;
				window.location.reload();
				this.router.navigate(['/guide/share-images.html']);
			}
		},
		(errorResponse: HttpErrorResponse) => {
			//this.errors = this.responseErrorComponent.responseError(errorResponse, this.form);
			this.processing = false;
		});
	}

}
