import { Component, Input, HostListener, Injector } from '@angular/core';
import { DataService } from '../services/data.service';
import Chart from 'chart.js/auto';
import { Router } from '@angular/router';
import { ModalService } from '../services/modal.service';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { createCustomElement } from '@angular/elements';
import { UntypedFormBuilder, Validators } from '@angular/forms';


@Component({
  selector: 'app-location',
  templateUrl: './location.component.html',
  styleUrls: ['./location.component.scss']
})
export class LocationComponent {

  @Input() pageId!: any;
  openModalGallery: boolean = false;
  configurations: any = {};
  page: any = null;
  selectedTrip = -1;
  locationTab = false;
  pageLoading = false;
  reminderForm:any;
  reminder: any;
  iframeVideo: SafeResourceUrl | undefined;
  selectBox = true;
  infoBox = true;
  isActive = true;
  hidetretangle = false;
  chart: any;
	minimum_price: any;
  selectedIndex = -1;
  tab = 1;
  tabName = 'Inbegrepen';
  reminderMsg: any;
  is_bookable: any;
  // isSticky: boolean = false;
  // @HostListener('window:scroll', ['$event'])
  // checkScroll() {
  //   this.isSticky = window.pageYOffset >=1040;
  //   console.log(window.pageYOffset);
  //   console.log()
  // }
  isSticky = false;

 

  // @HostListener('window:scroll', ['$event'])
  // checkScroll(event: Event) {
  //   const scrollPosition = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
  //   if (window.innerWidth >= 768) {
  //     this.isSticky = scrollPosition >= 1000; // Sticky for desktop view
  //   } else {
  //     this.isSticky = scrollPosition >= 2020; // Sticky for responsive view
  //   }
  // }
  // getTabData(tab: any, name: any){
	//   this.tab = tab;
	//   if (window.innerWidth <= 768) {
	// 	this.locationTab = true;
	// 	this.tabName = name;
	//   }
  // }
  
 
  isSticky1: boolean = false;
  tab1: any;
  locationTab1: boolean = false;
  tabName1: any;
	isSubmitted = false;
  @HostListener('window:scroll', ['$event'])
  checkScroll(event: Event) {
    const scrollPosition = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
    if (window.innerWidth >= 768) {
      this.isSticky = scrollPosition >= 1000; // Sticky for desktop view
    } else {
      this.isSticky = scrollPosition >= 2020; // Sticky for responsive view
    }
    const scrollPosition1 = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
    if (window.innerWidth >= 768) {
      this.isSticky1 = scrollPosition1 >= 1000; // Sticky for desktop view
    } else {
      this.isSticky1 = scrollPosition1 >=4350; // Sticky for responsive view
      console.log(scrollPosition1);
    }
  }
  getTabData(tab: any, name: any){
	  this.tab = tab;
	  if (window.innerWidth <= 768) {
		this.locationTab = true;
		this.tabName = name;
	  }
  }

  getTabData1(tab1: any, name1: any) {
    this.tab1 = tab1;
    if (window.innerWidth <= 768) {
      this.locationTab1 = true;
      this.tabName1 = name1;
    }
  }

  constructor(
	private dataService: DataService,
    private modalService: ModalService,
    private router: Router,
    private sanitizer: DomSanitizer,
	private injector: Injector,
	private fb: UntypedFormBuilder
  ) {
		this.reminderForm = this.fb.group({
      trip_id: [''],
			email: ['', Validators.required]
		});
  }
  

  ngOnInit(){
    this.dataService.configurationsSubject.subscribe((configurations: any) => {
      if(configurations.settings){
        this.configurations = configurations;
        this.getPageData();
      }
    });
    if (window.innerWidth <= 768) { // 768px portrait
        this.locationTab = true;
    }
  }

  getPageData(){
	this.pageLoading = true;
    this.dataService.getPageData(this.pageId).subscribe((response: any) => {
	this.page = response;
	this.minimum_price = ( Number(this.page.pageable.min_trip_fee) + Number(this.configurations.config.nature_disaster_insurance.value) + Number(this.configurations.config.sgr_bijdrage.value));
	this.is_bookable = this.configurations.config.is_bookable.value;
  this.page.pageable.packing_list = this.sanitizer.bypassSecurityTrustHtml(this.page.pageable.packing_list);
    
	this.pageLoading = false;
      this.dataService.setMeta({
        "title": this.page.meta_title,
        "description": this.page.meta_description,
        "keywords": this.page.meta_keywords,
        "date": this.page.updated_at
      });
      let chartData: any = [];
      let percentageTotal = 0;
      this.page.pageable.chart_values.map((chartLabel: any) => {
        let percentage = (parseInt(chartLabel)/this.page.pageable.chart_total_count)*100
        percentageTotal += percentage;
        chartData.push(percentage)
      });
      setTimeout(() => {
        this.chart = new Chart('ageGroupChart', {
          type: 'bar',
          data: {
            labels: this.page.pageable.chart_labels,
            datasets: [{
                data: chartData,
                backgroundColor: [
                    '#ec008c'
                ],
                borderColor: [
                    '#ec008c'
                ],
                borderWidth: 1
            }]
          },
          options: {
            scales: {
                y: {
                    beginAtZero: true,
                    ticks: {
                      callback: function(value: any){
                        return value+"%"
                      }
                    }
                }
            },
            plugins: {
                title: {
                    display: true,
                    text: 'Gemiddelde samenstelling'
                },
                legend: {
                    display: false,
                },
                tooltip: {
                  enabled: false
                }
            }
          }
      });
      }, 200);
      
    });
  }
	total_trip_fee(tripFee: any){
		return ( Number(tripFee) + Number(this.configurations.config.nature_disaster_insurance.value) + Number(this.configurations.config.sgr_bijdrage.value));
	}
  tripEndDate(trip: any){
    return (new Date(trip.start_date)).setDate((new Date(trip.start_date)).getDate() + trip.duration-1);
  }

  selectTrip(i: any){
    if(this.page.pageable.trips[i].space.remaining >= 1 && this.page.pageable.trips[i].is_not_bookable == 0){
      this.selectedTrip = i;
    }
  }

  redirect(type: string){
    this.router.navigate(['/' + type + '/' + this.page.pageable.trips[this.selectedTrip].start_date + '/' + this.page.page_name + '.html']);
  }

  showDiv(index: number) {
    this.selectedIndex = index;
  }

  selectIcon(){
    this.selectBox = !this.selectBox;
  }
  infoIcon(){
    this.infoBox = !this.infoBox;
  }
  locationTabIcon(){
    this.locationTab = !this.locationTab;
  }
  rectangleIcon(){
    this.hidetretangle = !this.hidetretangle;
  }

  openModal(id: string) {
    this.modalService.open(id);
  }
  
  popupGallery(){
	  this.openModalGallery = true;
  }

  closeModal(id: string) {
      this.modalService.close(id);
  }
  toggleActive() {
    this.isActive = !this.isActive;
  }
  submitReminderForm(event: any, tripId: any){
    this.reminderForm.get('trip_id').setValue(tripId);
		event.preventDefault();
		this.isSubmitted = true;
		if (!this.reminderForm.valid) return;
		this.reminder = this.reminderForm.value;
		if(tripId){
			this.dataService.storeReminderForm(this.reminder, tripId).subscribe((response: any) => {
        if(response.status==1){
          this.isSubmitted = false;
          this.reminderMsg = response.message;
          setTimeout(() => {
            // Code to execute after the specified time
            
            this.modalService.close('reminder_'+tripId);
          }, 3000); // 2000 milliseconds (2 seconds)
          
        }
      });
		}
		
	}
}
