import { Component, Input, SimpleChanges, ViewChild, ViewContainerRef, ComponentFactoryResolver, Self, Injector } from '@angular/core';
import { DataService } from '../services/data.service';
import { DomSanitizer } from '@angular/platform-browser';
import { createCustomElement } from '@angular/elements';
import { TripSliderComponent } from '../common/trip-slider/trip-slider.component';
import { UpcomingTripsComponent } from '../common/upcoming-trips/upcoming-trips.component';
import { UpcomingTripsSpotComponent } from '../common/upcoming-trips-spot/upcoming-trips-spot.component';
import { formatDate } from '@angular/common';
import { ActivatedRoute, Router, RouterState } from '@angular/router';

@Component({
  selector: 'app-page',
  templateUrl: './page.component.html',
  styleUrls: ['./page.component.scss']
})
export class PageComponent {
	@Input() pageId!: any;
	configurations: any = {};
	page: any = null;
	initDone = false;
	trips: any;

	pageLoading = false;
	
	attributeId = '';
	months:any = [];
	attributeOptions: any = [];
	monthOptions: any = [];
	comingMonths: any = [];
	showMonths = false;
	attributes: any = [];
	
	month: any;
	popularTrips: any = [];
	
	components: any[] = [
        TripSliderComponent,
		UpcomingTripsComponent,
		UpcomingTripsSpotComponent
    ];
	  
	constructor(
		private dataService: DataService,
		private sanitizer: DomSanitizer,
		private injector: Injector,
		private route: ActivatedRoute,
		private router: Router
	) {}

	ngOnInit(){
		const element = createCustomElement(TripSliderComponent, { injector: this.injector });
		const element1 = createCustomElement(UpcomingTripsComponent, { injector: this.injector });
		const element2 = createCustomElement(UpcomingTripsSpotComponent, { injector: this.injector });
    	customElements.define('app-trip-slider', element);
		customElements.define('app-upcoming-trips', element1);
		customElements.define('app-upcoming-trips-spot', element2);
		this.dataService.configurationsSubject.subscribe((configurations: any) => {
		  if(configurations.settings){
			this.configurations = configurations;
			this.initDone = true;
			this.getPageData();
			this.getMonths(null);
		  }
		});
	}
	
	getPageData(){
		this.pageLoading = true;
		this.dataService.getPageData(this.pageId).subscribe((response: any) => {
			this.page = response;
			this.page.content = this.sanitizer.bypassSecurityTrustHtml(this.page.content);
			this.attributes = this.page.attribures;
			this.popularTrips = this.page.popular_trips;
			this.setAttributeOptions();
			this.pageLoading = false;
			this.dataService.setMeta({
				"title": this.page.meta_title,
				"description": this.page.meta_description,
				"keywords": this.page.meta_keywords,
				"date": this.page.updated_at
			});
		});
	}

	ngOnChanges(changes: SimpleChanges) {
		if(this.initDone){
			this.getPageData();
		}
	}
	
	getMonths($event: any, firstLoad = false){
		this.dataService.getMonths($event).subscribe((months: any) => {
		  this.months = months;
		  this.setMonthOptions();
		  this.setComingMonths();
		});
	}

	setAttributeOptions(){
		this.attributeOptions = [];
		this.attributeOptions = this.attributes.map((item: any) => {
		  return {
			key: item.id,
			value: item.title
		  }
		});
		this.attributeOptions.unshift({
		  key: '',
		  value: 'Bekijk alle reizen'
		});
	}

	setMonthOptions(){
		this.monthOptions = [];
		this.monthOptions = this.months.map((item: any) => {
		  return {
			key: item.value,
			value: item.label + "(" + item.count + ")"
		  }
		});
		this.monthOptions.unshift({
		  key: '',
		  value: 'Bekijk alle data'
		});
	}

	setComingMonths(){
		let currentMonth = Number(formatDate((new Date()), "YYYYMM", "en"));
		let cnt=0;
		this.comingMonths = this.months.filter((item: any) => {
		  if(Number(item.value) > currentMonth && cnt < 3){
			cnt++;
			return item;
		  }
		});
	}
	
	submitForm(attribute_id:any, month:any){
		let urlTree = this.router.parseUrl(this.router.url);
		urlTree.queryParams['attribute_id'] = attribute_id;
		urlTree.queryParams['month'] = month;
		this.router.navigateByUrl(urlTree);
		//this.router.navigateByUrl('https://beta.simi-reizen.nl/');
	}
}
