import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { AuthService } from '../../../services/auth/auth.service';
import {DataService} from "../../../services/data.service";
import { HttpErrorResponse } from '@angular/common/http';
import { ModalService } from '../../../services/modal.service';

@Component({
  selector: 'app-tour-guide-add-edit',
  templateUrl: './tour-guide-add-edit.component.html',
  styleUrls: ['./tour-guide-add-edit.component.scss']
})
export class TourGuideAddEditComponent {
  configurations: any;
	pageLoading = false;
	authUser: any;
	trip: any;
	id: any;
  route_id;
  form: any;
  errors: any = {};
  processing;

  constructor( 
		private router: Router,  
		private authService: AuthService,
		private dataService: DataService,
		private route: ActivatedRoute,
		private fb: UntypedFormBuilder,
		private modalService: ModalService,
	) {
    this.route_id = 0;

    this.form = this.addEditForm('');
    this.route.params.subscribe(params => {
      if (params['id']) {
        this.route_id = Number(params['id']);
        this.authService.showMyGuide(this.route_id).subscribe((data: any) => {
          if (data) {
            this.form = this.addEditForm(data.guide);
            console.log(this.form);
          }
        }, (error: any) => {
          this.router.navigate(['guide/tourleader.html']);
        });
      }
    });
    this.processing = false;
  } 
  addEditForm(data: any) {
    return this.fb.group({
      id: [data.id ?? ''],
      name: [data.name ?? '', Validators.required],
      gender: [data.gender ?? ''],
      email: [data.email ?? ''],
      password: [data.password ?? ''],
      user_metas: this.fb.array([
        this.fb.group({key: 'gender', value: data.user_fields && data.user_fields.filter((rec: any) => rec.field_key == "gender").length ? data.user_fields.filter((rec: any) => rec.field_key == "gender")[0].field_value : 0}),
        //this.fb.group({key: 'partner', value: data.user_fields && data.user_fields.filter((rec: any) => rec.field_key == "partner").length ? data.user_fields.filter((rec: any) => rec.field_key == "partner")[0].field_value : 0}),
      ])

    });
    
  }
  submitForm(event: any) {
		event.preventDefault();
		if (!this.form.valid) return;

		this.processing = true;
		const guide = this.form.value;
   // this.fb.group({key: 'partner', value: data.user_fields && data.user_fields.filter((rec: any) => rec.field_key == "partner").length ? data.user_fields.filter((rec: any) => rec.field_key == "partner")[0].field_value : 0}),
		if (this.route_id) guide.id = this.route_id;
		this.authService.storeOrUpdateGuide(guide).subscribe((res: any) => {
      if(res.status){
        this.router.navigate(['guide/tourleader.html']);
      }
      this.processing = false;
		},
    (errorResponse: HttpErrorResponse) => {
      //this.errors = this.responseErrorComponent.responseError(errorResponse, this.form);
      this.processing = false;
    });
	}
	ngOnInit() {
    this.authService.authorize().then(result => {
			if (result) {
				this.authService.user.subscribe((response: any) => {
					this.authUser = response;
				});
			}
		});
		this.dataService.configurations.subscribe((configurations: any) => {
		  if(configurations.settings){
			this.configurations = configurations;
		  }
		});
	}
	getAge(year: number){
		const age = (new Date().getFullYear() - year );
		return age;
	}
	openModal(id: string) {
		this.modalService.open(id);
	}

	closeModal(id: string) {
	  this.modalService.close(id);
	}
}
