import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { AuthService } from '../../services/auth/auth.service';
import { HttpErrorResponse } from '@angular/common/http';


@Component({
  selector: 'app-guide-change-password',
  templateUrl: './guide-change-password.component.html',
  styleUrls: ['./guide-change-password.component.scss']
})
export class GuideChangePasswordComponent {
	pageLoading = false;
	authUser: any;
	changePasswordForm:any;
	userFrom: any;
	isSubmitted = false;
	
	constructor(
		private router: Router,  
		private authService: AuthService,
		private route: ActivatedRoute,
		private fb: UntypedFormBuilder
	) {
		this.changePasswordForm = this.fb.group({
			user_id: [''],
			name: [''],
			password: ['', Validators.required]
		});
	}
	ngOnInit() {
		this.authService.authorize().then(result => {
			if (result) {
				this.authService.user.subscribe((response: any) => {
					this.authUser = response;
					console.log(this.authUser);
					this.changePasswordForm.get('user_id').setValue(this.authUser.id);
					this.changePasswordForm.get('name').setValue(this.authUser.name);
				});
			}
		});
		
	}
	submitPassword(event: any){
		event.preventDefault();
		this.isSubmitted = true;
		if (!this.changePasswordForm.valid) return;
		this.userFrom = this.changePasswordForm.value;
		if(this.userFrom.user_id){
			this.authService.updateProfile(this.userFrom).subscribe((response: any) => {});
		}
		
	}
}
