import { Component, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { BlogService } from '../../services/blog.service';

@Component({
  selector: 'app-blog-single',
  templateUrl: './blog-single.component.html',
  styleUrls: ['./blog-single.component.scss']
})
export class BlogSingleComponent {
  @Input() pageId!: any;
  
	page: any = null;
	path = '';
  pageLoading = false;
  blogCategories: any;
  blogs: any;
  categories: any;

  constructor(private blogService: BlogService, private route: ActivatedRoute, ) {

    }

    ngOnInit(){
      this.route.url.subscribe((url: any) => {
        this.path = '';
        if(url.length > 0){
        for(let part of url){
          this.path += '/' + part.path.replace('.html', '')
        }
        this.path = this.path.slice(6);
        }
        this.getBlogSingleData(this.path);
      });
      
      
    }

	getBlogSingleData(Pagename: any,){
		if(!this.pageLoading){
			this.pageLoading = true;
			this.blogService.getBlogSingle(Pagename).subscribe((response: any) => {
				this.page = response;
				this.blogCategories = this.page.blog_categories;
				this.categories = this.page.categories;
				this.blogs = this.page.blogs;
				this.pageLoading = false;
				this.blogService.setMeta({
					"title": this.page.meta_title,
					"description": this.page.meta_description,
					"keywords": this.page.meta_keywords,
					"date": ""
				});
			});
			
		}
	}

}
