import { Injectable } from '@angular/core';
import {BehaviorSubject, Observable} from "rxjs";
import {HttpClient, HttpParams} from "@angular/common/http";
import {environment} from "../../environments/environment";

@Injectable({
  providedIn: 'root'
})
export class UpcomingTripService {

  constructor(private http:HttpClient) { }
  
  getUpcomingTrip(): Observable<any>{
    let queryParams = new HttpParams();
    return this.http.get(environment.apiUrl + '/upcoming-trips', {params: queryParams});
  }
  
  getUpcomingTripSpot(): Observable<any>{
    let queryParams = new HttpParams();
    return this.http.get(environment.apiUrl + '/upcoming-trips-spot', {params: queryParams});
  }
}
