import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  Input,
  NgZone,
  Output,
  Renderer2,
  ViewChild,
  EventEmitter
} from '@angular/core';

declare const $: any;

@Component({
  selector: 'app-slick',
  templateUrl: './slick.component.html',
  styleUrls: ['./slick.component.scss']
})
export class SlickComponent {

  constructor(private renderer: Renderer2, private ngZone: NgZone) {}

  @Input() class: string | undefined;

  @Input() config!: any;

  @ViewChild('el', { read: ElementRef }) el: ElementRef | undefined;

  ngOnInit() {
    if(typeof $ !== 'undefined'){
      $(window).on("load", () => {
        return;
        // if(this.el) {
        //   $(this.el.nativeElement).slick(this.config);
        // }
      });
    }
  }
  ngAfterViewInit() {
    if(this.el && typeof $ !== 'undefined') {
      setTimeout(() => {
        if(this.el) {
          $(this.el.nativeElement).slick(this.config);
        }
      }, 500);
    }
  }
}
