import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { ModalService } from '../../services/modal.service';
import { AuthService } from '../../services/auth/auth.service';
import {DataService} from "../../services/data.service";
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-booking-details',
  templateUrl: './booking-details.component.html',
  styleUrls: ['./booking-details.component.scss']
})
export class BookingDetailsComponent {
	configurations: any;
	pageLoading = false;
	authUser: any;
	whatsappNumberForm:any;
	passportDetailsForm:any;
	insuranceForm:any;
	addonForm:any;
	insurance: any = [];
	addon:any = [];
	insurances: any = [];
	whatsappNumber: any;
	isSubmitted = false;
	booking: any;
	airlineDetails = false;
	isWhatsapp = false;
	isPassportDetails = true;
	id: any;
	passportDetails: any;
	paymentLinks: any;
	passportMsg :any;
	constructor(
		private modalService: ModalService, 
		private router: Router,  
		private authService: AuthService,
		private dataService: DataService,
		private route: ActivatedRoute,
		private fb: UntypedFormBuilder
	) {
		this.whatsappNumberForm = this.fb.group({
			booking_id: [''],
			whatsapp_number: ['', Validators.required]
		});
		this.passportDetailsForm = this.fb.group({
			booking_id: [''],
			document_number: ['', Validators.required],
			issue_date: ['', Validators.required],
			expiry_date: ['', Validators.required]
		});
		this.insuranceForm = this.fb.group({
			booking_id: [''],
			insurance: [''],
		});
		this.addonForm = this.fb.group({
			is_confirm: [''],
			booking_id: [''],
			addon_id: [''],
			extra_field_1: [''],
			extra_field_2: [''],
			extra_field_3: [''],
		});
	}
	
	getBookingData(key: any){
		this.pageLoading = true;
		this.authService.getBookingData(key).subscribe((response: any) => {
		  this.booking = response.booking;
		  this.paymentLinks = response.paymentLinks;
			if(this.booking.passport_details.length>0){
				this.passportDetailsForm.get('document_number').setValue(this.booking.passport_details[0].document_number);
				this.passportDetailsForm.get('issue_date').setValue(this.booking.passport_details[0].issue_date);
				this.passportDetailsForm.get('expiry_date').setValue(this.booking.passport_details[0].expiry_date);
				this.isPassportDetails = false;
			}
		  this.whatsappNumberForm.get('booking_id').setValue(this.booking.id);
		  this.passportDetailsForm.get('booking_id').setValue(this.booking.id);
		  this.insuranceForm.get('booking_id').setValue(this.booking.id);
		  this.pageLoading = false;
		  this.dataService.setMeta({
			"title": "Mijn Simi - beheer je reis",
			"description": "",
			"keywords": "",
			"date": ''
		  });
		});
	}
	ngOnInit() {
		this.authService.authorize().then(result => {
			if (result) {
				this.authService.user.subscribe((response: any) => {
					this.authUser = response;
				});
			}
		});
		this.dataService.configurations.subscribe((configurations: any) => {
		  if(configurations.settings){
			this.configurations = configurations;
		  }
		});
		this.route.url.subscribe((url: any) => {
			this.id = '';
			if(url.length > 0){
			for(let part of url){
			  this.id += '/' + part.path.replace('.html', '')
			}
			this.id = this.id.slice(16);
			console.log(this.id);
			}
			this.getBookingData(this.id);
		});
	}
	inviteBox = true;
	cronBox = true;
	versionBox = true;

	InviteIcon(){
		this.inviteBox = !this.inviteBox;
	}
	cronBoxIcon(){
		this.cronBox = !this.cronBox;
	}
	versionIcon(){
		this.versionBox = !this.versionBox;
	}

	openModal(id: string) {
		this.modalService.open(id);
	}

	closeModal(id: string) {
	  this.modalService.close(id);
	}
	isConfirm(is_confirm: number) {
		this.addonForm.get('is_confirm').setValue(is_confirm);
	}
	openModalAddon(box: string, id: number) {
		this.modalService.open(box);
		const addon = this.booking.addons.filter( (rec: any) => rec.id == id);
		console.log(addon);
		if(addon){
			this.addonForm.get('booking_id').setValue(this.booking.id);
			this.addonForm.get('addon_id').setValue(addon[0].id);
			this.addonForm.get('extra_field_1').setValue(addon[0].extra_field_1);
			this.addonForm.get('extra_field_2').setValue(addon[0].extra_field_2);
			this.addonForm.get('extra_field_3').setValue(addon[0].extra_field_3);
		}
	}

	closeModalAddon(box: string, id: number) {
	  this.modalService.close(box);
	}
	getAirlineDetails() {
	  this.airlineDetails = !this.airlineDetails;
	}
	whatsappForm() {
	  this.isWhatsapp = !this.isWhatsapp;
	}
	showPassportForm() {
	  this.isPassportDetails = !this.isPassportDetails;
	}
	getAge(year: number){
		const age = (new Date().getFullYear() - year );
		return age;
	}
	dueDate(date: any){
		var today = new Date(date);
		var yesterday = new Date(today.getTime() - (42*24*3600));
		console.log(yesterday);
		return yesterday;
	}
	getInsurances(isChecked: any, value: string){
		const index = this.insurances.findIndex((x:any) => x == value);
		if(isChecked.checked && index == -1){
		  this.insurances.push(value);
		}
		else if(!isChecked.checked && index >= 0){
		  this.insurances.splice(index, 1);
		}
		this.insuranceForm.get('insurance').setValue(this.insurances);
	}
	submitwhatsappNumber(event: any){
		event.preventDefault();
		this.isSubmitted = true;
		if (!this.whatsappNumberForm.valid) return;
		this.whatsappNumber = this.whatsappNumberForm.value;
		if(this.whatsappNumber.booking_id){
			this.authService.storeWhatsappNumber(this.whatsappNumber).subscribe((response: any) => {});
		}
		
	}
	submitPassportDetails(event: any){
		event.preventDefault();
		this.isSubmitted = true;
		if (!this.passportDetailsForm.valid) return;
		this.passportDetails = this.passportDetailsForm.value;
		if(this.passportDetails.booking_id){
			this.authService.updatePassportDetails(this.passportDetails).subscribe((response: any) => {
				if(response.status){
					this.passportMsg = "Passport details successfully submitted.";
					this.isSubmitted = false;
				}
			});
		}
	}
	submitInsurance(event: any){
		event.preventDefault();
		this.isSubmitted = true;
		if (!this.insuranceForm.valid) return;
		this.insurance = this.insuranceForm.value;
		if(this.insurance.booking_id){
			this.authService.updateInsurance(this.insurance).subscribe((response: any) => {});
		}
	}
	submitAddon(event: any){
		event.preventDefault();
		this.isSubmitted = true;
		if (!this.addonForm.valid) return;
		this.addon = this.addonForm.value;
		if(this.addon.booking_id){
			this.authService.updateAddon(this.addon).subscribe((response: any) => {
				if(response.status){
					this.modalService.close("addon");
				}
			});
		}
		
	}
}
