import { Component, Input, SimpleChanges, ViewChild, ViewContainerRef, ComponentFactoryResolver, Self, Injector } from '@angular/core';
import { DataService } from '../services/data.service';
import { SitemapService } from '../services/sitemap.service';
import { Router } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-sitemap',
  templateUrl: './sitemap.component.html',
  styleUrls: ['./sitemap.component.scss']
})
export class SitemapComponent {
	
	configurations: any = {};
	page: any = null;
	pages: any = [];
	locations: any = [];
	blogs: any = [];
	pageLoading = false;
	
	constructor(
		private dataService: DataService,
		private sitemapService: SitemapService,
		private router: Router,
		private sanitizer: DomSanitizer,
	) {}
	
	ngOnInit(){
		this.dataService.configurationsSubject.subscribe((configurations: any) => {
		  if(configurations.settings){
			this.configurations = configurations;
		  }
		});
		this.getPageData();
	}
	
	getPageData(){
		this.pageLoading = true;
		this.dataService.getPageData(322).subscribe((response: any) => {
			this.page = response;
			this.page.content = this.sanitizer.bypassSecurityTrustHtml(this.page.content);
			this.dataService.setMeta({
				"title": this.page.meta_title,
				"description": this.page.meta_description,
				"keywords": this.page.meta_keywords,
				"date": this.page.updated_at
			});
		});
		this.sitemapService.getData().subscribe((response: any) => {
			this.locations = response.locations;
			this.pages = this.configurations.pages;
			this.blogs = response.blogs;
			this.pageLoading = false;
		});
	}
}