import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { AuthService } from '../../../services/auth/auth.service';
import {DataService} from "../../../services/data.service";
import { HttpErrorResponse } from '@angular/common/http';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

@Component({
  selector: 'app-quizes-result',
  templateUrl: './quizes-result.component.html',
  styleUrls: ['./quizes-result.component.scss']
})
export class QuizesResultComponent {

  // answers: {
  //   2:[4],
  //   3:[4,2,1],
  // }


  	answers:any = {answer:[]};


	configurations: any;
	pageLoading = false;
	authUser: any;
	lesson: any = [];
	id: any;
	course: any;
	questionForm:any;

	constructor(
		private router: Router,
		private authService: AuthService,
		private dataService: DataService,
		private route: ActivatedRoute,
		private fb: UntypedFormBuilder
	) {
		this.questionForm = this.fb.group({
			course_id: [''],
		});
	}

  selectAnswers(check_multiple: any, question_id: any, option: any){
    console.log(question_id);
    console.log(option);
    console.log(check_multiple);
    if(check_multiple == 0){
      this.answers.answer[question_id] = option;
    }
    if(check_multiple == 1){
      if(question_id){
        this.answers.answer[question_id].push(option);
      }
    }
    console.log(this.answers);
  }

	getQuestions(key: any){
		this.pageLoading = true;
		this.authService.getQuestions(key).subscribe((response: any) => {
		  this.lesson = response;
		  this.pageLoading = false;
		});
	}
	ngOnInit() {
		this.authService.authorize().then(result => {
			if (result) {
				this.authService.user.subscribe((response: any) => {
					this.authUser = response;
				});
			}
		});
		this.dataService.configurations.subscribe((configurations: any) => {
		  if(configurations.settings){
			this.configurations = configurations;
		  }
		});
		this.route.url.subscribe((url: any) => {
			this.id = '';
			if(url.length > 0){
				for(let part of url){
				  this.id += '/' + part.path.replace('.html', '')
				}
				this.id = this.id.slice(20);
			}
			this.getQuestions(this.id);
		});
	}

}
