import { environment } from './../../environments/environment';
import {Component, Input, OnInit} from '@angular/core';
import {DataService} from "../services/data.service";
import {ActivatedRoute, Router} from "@angular/router";
import { formatDate } from '@angular/common';


@Component({
  selector: 'app-default',
  templateUrl: './default.component.html',
  styleUrls: ['./default.component.scss']
})
export class DefaultComponent {
  configurations: any;
  path = '';
  page = '';
  month = '';
  pageId = 0;
  loaded = false;
  appUrl: any;
  
  constructor(private dataService: DataService,
              private route: ActivatedRoute, private router: Router) {
        this.appUrl = environment.appUrl;
  }

  ngOnInit(){
    this.dataService.configurationsSubject.subscribe((configurations: any) => {
      if(configurations.settings){
        this.configurations = configurations;
        this.parsePath();
      }
    });
    this.route.url.subscribe((url: any) => {
      this.path = '';
      if(url.length > 0){
        for(let part of url){
          this.path += '/' + part.path.replace('.html', '')
        }
        this.path = this.path.slice(1);
        // this.dataService.checkRedirectUrl(this.appUrl+'/'+this.path+'.html').subscribe((response: any) => {
        //     if(response.status){
        //       if(response.pageRedirect.redirect_url){
        //         window.location.href = response.pageRedirect.redirect_url;
        //       }
        //     }
        // });
      }
      this.parsePath();
    });
  }
  parsePath(){
    if(!this.configurations){
      return;
    }
    //if empty
    if(!this.path){
      this.page = 'home';
    }
    else{
      //if it is a month
      this.configurations.pages.some((page: any) => {
        if(this.path === page.page_name){
          //Landing Pages
          if(page.type === 'landingpage'){
            let today = new Date();
            if(today.getMonth() > page.extra.month){
              today.setFullYear(today.getFullYear() + 1);
            }
            today.setMonth(page.extra.month);
            this.month = formatDate(today, "YYYYMM", "en");
            this.page = 'home';
          }
          else{
            this.loaded = true;
            this.page = page.type;
            this.pageId = page.id;
          }

          return true;
        }
        return false;
      });
    
       // If no matching page is found
    // if (!foundPage) {
    //   this.page = 'default';
    //   this.router.navigate(['/home']); // Navigate to the home page
    //   return;
    // }

    const foundPage = this.configurations.pages.some((page: any) => {
      if (this.path === page.page_name) {
        return true;
      }
      return false;
    });
    if (!foundPage) {
      this.page = 'default';
      this.router.navigate(['']);
      return;
    }
    }
  }
}
