import { Component } from '@angular/core';
import { DataService } from '../services/data.service';
import { AuthService } from '../services/auth/auth.service';


@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})

export class HeaderComponent {

  configurations: any = {};
  headerMenu: any = {};
  pageLoading = false;
  authUser: any;
  mobileMenu = true;
  mobileSubMenu: any = [];
  mobileSubinnerMenu: any = [];
  
  is_login = false;

  constructor(
    private dataService: DataService,
	private authService: AuthService,
  ) {

  }

  ngOnInit(){
      this.pageLoading = true;
      this.dataService.configurationsSubject.subscribe((configurations: any) => {
        if(configurations.settings){
        this.configurations = configurations;
        this.headerMenu = configurations.frontMenus;
        this.pageLoading = false;
        }
      });
	
      this.authService.authorize().then(result => {
        if (result) {
          this.authService.user.subscribe((response: any) => {
            this.authUser = response;
            this.is_login = true;
          })
        }
      });
	
  }
  mobileMenuIcon(){
    this.mobileMenu = !this.mobileMenu;
  }
  mobileSubMenuIcon(index: number){
    this.mobileSubMenu[index] = !this.mobileSubMenu[index];
  }
  mobileSubinnerMenuIcon(index: number){
    this.mobileSubinnerMenu[index] = !this.mobileSubinnerMenu[index];
  }
}
