import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-response-error',
  templateUrl: './response-error.component.html',
  styleUrls: ['./response-error.component.scss']
})
export class ResponseErrorComponent implements OnInit{

  errorMsg: any;
  unkonownError:any;
  errors: any;
  formError: any;

  constructor() { }

  ngOnInit(): void {
  }

  responseError(errorResponse: HttpErrorResponse, form: any){
    this.unkonownError = '';
    if((errorResponse.statusText) == 'Unknown Error' || (errorResponse.status == 401)){
      this.unkonownError = 'Something went wrong. Try again';
    }
    this.errors = errorResponse.error['errors'];
    if(this.errors){
      for(let key in this.errors){
        if(form.controls[key]){
          form.controls[key].setErrors({'incorrect': true});
        }
      }
      return this.errors;
    }
  }
}
