import { Component } from '@angular/core';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-bestemmingen',
  templateUrl: './bestemmingen.component.html',
  styleUrls: ['./bestemmingen.component.scss']
})
export class BestemmingenComponent {
  pageLoading = false;
  configurations: any;
  footer_partners_text: any = {};
  page:any = null;
  meta_title:any;
  meta_description:any;
  constructor(private dataService: DataService){

  }
  ngOnInit(){
	  if(!this.pageLoading){
		  this.pageLoading = true;
    this.dataService.configurationsSubject.subscribe((configurations: any) => {
		console.log(configurations);
			 this.pageLoading = false;
		  if(configurations.settings){
        this.configurations = configurations;
        this.page = this.configurations.pages.filter((item: any) =>  item.id === 409)[0];
        console.log(this.page);
        this.footer_partners_text = this.configurations.config.fooer_partners_text.value;
        // this.meta_title = this.configurations.config.destination_meta_title.value;
        // this.meta_description = this.configurations.config.destination_meta_description.value;
        this.dataService.setMeta({
          "title": this.page.meta_title,
          "description": this.page.meta_description,
          "keywords": "",
          "date": ""
        });
		  }
		
    });
	  }
  }
}
