import { Component, Input, OnInit, SimpleChanges, ElementRef  } from '@angular/core';
import { DataService } from '../../services/data.service';
import { UpcomingTripService } from '../../services/upcoming-trip.service';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-upcoming-trips-spot',
  templateUrl: './upcoming-trips-spot.component.html',
  styleUrls: ['./upcoming-trips-spot.component.scss']
})
export class UpcomingTripsSpotComponent {
	pageLoading = false;
	content: any;
	parsedJson: any;
	viewContent: any;
	constructor(
		private dataService: DataService,
		private upcomingTripService: UpcomingTripService,
		private sanitizer: DomSanitizer
	) {	}
	ngOnInit(){
		if(!this.pageLoading){
			this.pageLoading = true;
			this.upcomingTripService.getUpcomingTripSpot().subscribe((response: any) => {
				this.content = JSON.stringify(response);  
				this.parsedJson = JSON.parse(this.content);
				this.viewContent = this.sanitizer.bypassSecurityTrustHtml(this.parsedJson);
				console.log(this.viewContent);
				this.pageLoading = false;
				
			});
		}
		
	}
}
