import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { ModalService } from '../../services/modal.service';
import { AuthService } from '../../services/auth/auth.service';
import {DataService} from "../../services/data.service";

@Component({
  selector: 'app-guide-dashboard',
  templateUrl: './guide-dashboard.component.html',
  styleUrls: ['./guide-dashboard.component.scss']
})
export class GuideDashboardComponent {
	configurations: any;
	pageLoading = false;
	authUser: any;
	trips:any = [];
	is_partner = false;
	data: any;
	constructor(
		private modalService: ModalService, 
		private router: Router,  
		private authService: AuthService,
		private dataService: DataService
	) {}
	
	getGuideTrips(){
		if(!this.pageLoading){
		  this.pageLoading = true;
		  this.authService.getGuideTrips().subscribe((response: any) => {
			this.data = response;
			this.trips = response.user.trips;
			this.data.user.fields.filter((item: any) => {
				if(item.field_key=='is_partner' && item.field_value=='1'){
					this.is_partner = true;
				}
			  });
			this.pageLoading = false;
			this.dataService.setMeta({
			  "title": "",
			  "description": "",
			  "keywords": "",
			  "date": ''
			});
		  });
		}
	}
	ngOnInit() {
		this.authService.authorize().then(result => {
			if (result) {
				this.authService.user.subscribe((response: any) => {
					this.authUser = response;
				})
			}
		});
		this.dataService.configurations.subscribe((configurations: any) => {
		  if(configurations.settings){
			this.configurations = configurations;
			this.getGuideTrips();
		  }
		});
	}

}
