import { Inject, Injectable } from '@angular/core';
import {BehaviorSubject, Observable} from "rxjs";
import {HttpClient, HttpParams} from "@angular/common/http";
import {environment} from "../../environments/environment";
import {Meta, Title} from "@angular/platform-browser";
import { DOCUMENT } from '@angular/common';

@Injectable({
  providedIn: 'root'
})
export class BlogService {
	configurationsSubject = new BehaviorSubject<any>({});
	public configurations = this.configurationsSubject.asObservable();
  	constructor(
		private http:HttpClient, 
		private titleService: Title,
		private metaTagService: Meta) { }


  	getBlog(search: any, pageNumber: number, seoUrl: any): Observable<any>{
		let queryParams = new HttpParams();
		if(search)
			Object.keys(search).forEach(function (key) {
				queryParams = queryParams.append(key, search[key]);
			});
		if(pageNumber)
			queryParams = queryParams.append("page", pageNumber);
		
		if(seoUrl)
		queryParams = queryParams.append("seo_url", seoUrl);

		return this.http.get(environment.apiUrl + '/getBlogs',{
			params: queryParams
		});
	}

	getBlogSingle(Pagename: any): Observable<any>{
		let queryParams = new HttpParams();

		if(Pagename)
		queryParams = queryParams.append("pagename", Pagename);
		return this.http.get(environment.apiUrl + '/getSingleblog', {
			params: queryParams});

	}

	setMeta(meta: any){
		this.titleService.setTitle(meta.title);
		this.metaTagService.removeTag("name=keywords");
		this.metaTagService.removeTag("name=meta_title");
		this.metaTagService.removeTag("name=meta_description");
		this.metaTagService.removeTag("name=date");
		this.metaTagService.addTags([
		{
		  name: 'keywords',
		  content: meta.keywords ?? '',
		},
		{
		  name: 'meta_title',
		  content: meta.title ?? '',
		},
		{
		  name: 'meta_description',
		  content: meta.description ?? '',
		},
		{ name: 'date', content: meta.date, scheme: 'YYYY-MM-DD' }
	  ]);
	}
}
