import {Component, ElementRef, OnInit} from '@angular/core';

@Component({
  selector: 'app-image',
  templateUrl: './image.component.html',
  inputs: ['key', 'title', 'containerheight'],
  styleUrls: ['./image.component.scss']
})
export class ImageComponent implements OnInit {
  cloudFrontUrl = 'https://d1n5x7e4cmsf36.cloudfront.net';
  url = '';
  key:  string|null = null;
  width = 0;
  height = 0;
  containerheight = '';
  title: string|null = null;
  constructor(private el:ElementRef) {
  }

  ngOnInit(): void {
    //this.key = 'app/jongerenreizen.jpeg';
    this.width = this.el.nativeElement.parentElement.offsetWidth;
    if(this.containerheight){
      this.height = this.el.nativeElement.parentElement.offsetHeight;
    }
    this.generateUrl();
  }

  onResize(){
    this.width = this.el.nativeElement.parentElement.offsetWidth;
    if(this.containerheight){
      this.height = this.el.nativeElement.parentElement.offsetHeight;
    }
    this.generateUrl();
  }

  generateUrl(){
    let resize:any = {
      width: this.width
    }
    if(this.height){
      resize.height = this.height
    }
    const imageRequest = JSON.stringify({
      bucket: "simi-reizen",
      key: this.key,
      edits: {
        resize: resize,
        quality: 100
      }
    });
    this.url = `${this.cloudFrontUrl}/${btoa(imageRequest)}`;
  }

  ngOnDestroy() {
  }
}
