import { Component } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { SupportService } from '../services/support.service';
import {DataService} from "../services/data.service";
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent {
	configurations: any;
	support_categories: any;
	search = new FormControl();
	path = '';
	pageLoading = false;
	page:any = null;
	footer_partners_text: any = {};
	contact_details_title: any = {};
	contact_col_1: any = {};
	contact_col_2: any = {};
	contact_col_3: any = {};
	contact_col_4: any = {};
	google_map: SafeResourceUrl | undefined;
	meta_title: any = {};
	meta_description: any = {};

  constructor(
		private supportService: SupportService,
		private route: ActivatedRoute,
		private router: Router,
		private dataService: DataService,
		private sanitizer: DomSanitizer
	) {}

  ngOnInit(){
	this.dataService.configurationsSubject.subscribe((configurations: any) => {
		console.log(configurations);
		if(configurations.settings){
		  this.configurations = configurations;
		  this.footer_partners_text = this.configurations.config.fooer_partners_text.value;
		  this.contact_details_title = this.configurations.config.contact_details_title.value;
		  this.contact_col_1 = this.configurations.config.contact_col_1.value;
		  this.contact_col_2 = this.configurations.config.contact_col_2.value;
		  this.contact_col_3 = this.configurations.config.contact_col_3.value;
		  this.contact_col_4 = this.configurations.config.contact_column_4.value;
		//   this.meta_title = this.configurations.config.contact_page_title.value;
		//   this.meta_description = this.configurations.config.contact_page_description.value;
		  this.google_map = this.sanitizer.bypassSecurityTrustHtml(this.configurations.config.google_map.value);
		}
	});
	this.route.url.subscribe((url: any) => {
	  this.path = '';
	  if(url.length > 0){
		for(let part of url){
		  this.path += '/' + part.path.replace('.html', '')
		}
		this.path = this.path.slice(8);
	  }
	  this.getSupportArticleData('', this.path);
	});
    /*this.supportService.getSupportCategories().subscribe((res: any) => {
      this.support_categories = res;
    });*/
  }
	getSupportArticleData(search: any, seoUrl: string){
		console.log(this.meta_title);
		if(!this.pageLoading){
		  this.pageLoading = true;
		  this.supportService.getSupportArticleData({search}, seoUrl).subscribe((response: any) => {
			this.support_categories = response.articles;
			this.page = response.page;
			this.pageLoading = false;
			this.supportService.setMeta({
				"title": this.page.meta_title,
				"description": this.page.meta_description,
				"keywords": this.page.meta_keywords,
				"date": ""
			});
		  });
		}
	}

  searchArticle(value: string){
	this.router.navigate(['/contact'], { queryParams: { s: value } });
  }

  removeType(){
    localStorage.setItem('contactPageSearch', '');
  }

}
