import { Injectable } from '@angular/core';
import {BehaviorSubject, Observable} from "rxjs";
import {HttpClient, HttpParams} from "@angular/common/http";
import {environment} from "../../environments/environment";

@Injectable({
  providedIn: 'root'
})
export class VakantiesService {

	constructor(private http:HttpClient) { }
	getVakanties(filters: any, page: number): Observable<any>{
		let queryParams = new HttpParams();
		Object.keys(filters).forEach(function (key) {
			if(key == 'trip_types'){
				Object.keys(filters[key]).forEach(function (key2) {
					queryParams = queryParams.append('trip_types['+key2+']', filters[key][key2]);
				});
			}
			else{
				queryParams = queryParams.append(key, filters[key]);
			}
		});
		queryParams = queryParams.append('page', page);
		return this.http.get(environment.apiUrl + '/getTrips',{
			params: queryParams
		});
	}
	getTripTypes(): Observable<any>{
		let queryParams = new HttpParams();
		return this.http.get(environment.apiUrl + '/getTripTypes', {params: queryParams});
	}
}
