import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import {AuthService} from "../services/auth/auth.service";
import {FormBuilder, UntypedFormControl, UntypedFormGroup, Validators} from "@angular/forms";
import {environment} from "../../environments/environment";
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class LoginComponent implements OnInit {
  hide = true;
  form: any;
  loading = false;
  errors: any = {};
  isForgotPasswordFormActive = false;
  codeSent = false;
  codeVerified = false;
  id: any;
  
  constructor(
    fb: FormBuilder,
    private authService: AuthService,
    private router: Router,
    private route: ActivatedRoute
  ) {
      this.form = fb.group({
        email: ['', [Validators.required, Validators.email]],
        password: ['', Validators.required],
        token: [''],
      });
  }
	FrontLogin(user_id: number) {
		this.authService.FrontLogin(user_id).subscribe((response: any) => {
			if(response.status){
        if(response.user.is_tour_guide){
          this.router.navigate(['/guide/dashboard/list.html']);
        }
        else{
          this.router.navigate(['/mijn/dashboard/list.html']);
        }
				
			}
		});
		
	}
  ngOnInit(): void {
	  this.route.queryParams.subscribe((user_id: any) => {
			this.id = user_id.user_id;
			console.log(this.id);
			
			this.FrontLogin(this.id);
		});
  }

  setForgotPasswordForm(status: boolean): void{
    this.isForgotPasswordFormActive = status;
    if(status){
      this.form.controls.password.clearValidators();
      this.form.controls.token.addValidators(Validators.required);
    }
    else{
      this.form.controls.password.addValidators(Validators.required);
      this.form.controls.token.clearValidators();
    }
    this.form.controls.password.updateValueAndValidity();
  }

  setCodeSent(status: boolean): void{
    this.codeSent = status;
    this.codeVerified = false;
  }

  login(): void {
    if(this.form.valid && !this.loading){
			this.errors = {};
      this.loading = true;
      if(this.isForgotPasswordFormActive){
        if(this.codeSent){
          if(this.codeVerified){
            this.authService.resetPassword(this.form.controls.email.value, this.form.controls.token.value, this.form.controls.password.value)
              .subscribe((res: any) => {
                if(res.status){
                  this.authService.setCurrentUser(res.user);
                  window.location.href = environment.appUrl+'/';
                }
                else{
                  this.errors = res.error;
                }
                this.loading = false;
              }, (error: any) => {
                if(error.status == 422){
                  this.errors = error.error.errors;
                  if(this.errors.errors){
                    for(let key in this.errors.errors){
                      if(this.form.controls[key]){
                        this.form.controls[key].setErrors({'incorrect': true});
                      }
                    }
                  }
                }
                this.loading = false;
              });
          }
          else{
            this.authService.verifyToken(this.form.controls.email.value, this.form.controls.token.value)
              .subscribe((res: any) => {
                if(res.status){
                  this.codeVerified = true;
                }
                else{
                  this.errors = res.error;
                }
                this.loading = false;
              }, (error: any) => {
                if(error.status == 422){
                  this.errors = error.error.errors;
                  if(this.errors.errors){
                    for(let key in this.errors.errors){
                      if(this.form.controls[key]){
                        this.form.controls[key].setErrors({'incorrect': true});
                      }
                    }
                  }
                }
                this.loading = false;
              });
            }
        }
        else{
          this.authService.forgotPassword(this.form.controls.email.value)
            .subscribe((res: any) => {
              if(res.status){
                this.codeSent = true;
              }
              else{
                this.errors = res.error;
              }
              this.loading = false;
            }, (error: any) => {
              if(error.status == 422){
                this.errors = error.error.errors;
                if(this.errors.errors){
                  for(let key in this.errors.errors){
                    if(this.form.controls[key]){
                      this.form.controls[key].setErrors({'incorrect': true});
                    }
                  }
                }
              }
              this.loading = false;
            });
          }
      }
      else{
        this.authService.login(this.form.controls.email.value, this.form.controls.password.value)
          .subscribe((res: any) => {
            if(res.status){
              this.authService.setCurrentUser(res.user);
				      if(res.user.is_tour_guide){
					      window.location.href = environment.appUrl+'/guide/dashboard/list.html';
			  	    }
				      else{
					      window.location.href = environment.appUrl+'/mijn/dashboard/list.html';
				      }

            }
            else{
              this.errors = res.error;
            }
            this.loading = false;
          }, (error: any) => {
            if(error.status == 422){
              this.errors = error.error.errors;
              if(this.errors.errors){
                for(let key in this.errors.errors){
                  if(this.form.controls[key]){
                    this.form.controls[key].setErrors({'incorrect': true});
                  }
                }
              }
            }
            this.loading = false;
          });
      }
    }
  }
}
