import { FormGroup } from '@angular/forms';

// custom validator to check that two fields match
export function PostCode(country: string, postcode: string) {
	return (formGroup: FormGroup) => {
		const countryField = formGroup.controls[country] ?? 0;
		const postCodeField = formGroup.controls[postcode];

		// set error on matchingControl if validation fails
    if(!postCodeField.value){ postCodeField.setErrors({ required: true }); }
		if ( countryField.value == 1 && (postCodeField.value && (!postCodeField.value.match('[1-9][0-9]{3}')) || postCodeField.value.length > 4)) {
        postCodeField.setErrors({ postCode: true });
		}
    else if( countryField.value == 0 && (postCodeField.value && (!postCodeField.value.match('[1-9][0-9]{3} ?(?!sa|sd|ss)[a-z,A-Z]{2}')) || postCodeField.value.length > 7)){
        postCodeField.setErrors({ postCode: true });
    } else {
        postCodeField.setErrors(null);
		}
	}
}
