import {Component, Input, OnInit, SimpleChanges} from '@angular/core';
import {DataService} from "../services/data.service";
import {ActivatedRoute, Router} from "@angular/router";
import { formatDate } from '@angular/common';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit{

  configurations: any;
  params: any;
  initDone = false;
  pageLoading = false;

  slideConfig = { dots: false, infinite: false, slidesToShow: 3, slidesToScroll: 3, autoplay: false, autoplaySpeed: 3000, responsive: [{ breakpoint: 900, settings: { slidesToShow: 2, slidesToScroll: 1, } }, { breakpoint: 800, settings: { slidesToShow: 2, slidesToScroll: 2, } }, { breakpoint: 600, settings: { slidesToShow: 1, slidesToScroll: 1, centerMode: true, } }] };
  slideDestination = { dots: false, infinite: false, slidesToShow: 4, slidesToScroll: 3, autoplay: false, autoplaySpeed: 3000, responsive: [{ breakpoint: 900, settings: { slidesToShow: 2, slidesToScroll: 1, } }, { breakpoint: 800, settings: { slidesToShow: 2, slidesToScroll: 2, } }, { breakpoint: 600, settings: { slidesToShow: 1, slidesToScroll: 1, centerMode: true, } }] };

  selectBox = true;
  selectYear = true;
  selectMonth = true;

  showAgeGroups = false;
  showMonths = false;

  attributeId = '';
  months:any = [];
  attributeOptions: any = [];
  monthOptions: any = [];
  comingMonths: any = [];

  pageData:any = [];
  page:any = null;
  attributes: any = [];
  destinations: any = [];
  blogs: any = [];
  footer_partners_text: any = {};
  total_score: any;
  nature_disaster_insurance: any;
  sgr_bijdrage: any;

  @Input() month!: any;

  constructor(private dataService: DataService,
    private route: ActivatedRoute) {

  }

  getPageData(){
    if(!this.pageLoading){
      this.pageLoading = true;
      this.dataService.getHomePageData(this.attributeId, this.month, this.params).subscribe((response: any) => {
        this.pageData = response.trips;
		    this.nature_disaster_insurance = Number(this.configurations.config.nature_disaster_insurance.value);
		    this.sgr_bijdrage =	Number(this.configurations.config.sgr_bijdrage.value);
		    this.page = response.page;
        this.attributes = response.attributes;
        this.destinations = response.destinations;
        this.blogs = response.blogs;
        this.setAttributeOptions();
        this.pageLoading = false;
        this.dataService.setMeta({
          "title": this.page.meta_title,
          "description": this.page.meta_description,
          "keywords": this.page.meta_keywords,
          "date": this.page.updated_at
        });
      });
    }
  }

  ngOnInit(){
    this.route.queryParams.subscribe((params: any) => {
      this.initDone = true;
      this.params = params;
      this.dataService.configurationsSubject.subscribe((configurations: any) => {
        if(configurations.settings){
          this.configurations = configurations;
          this.getMonths(null);
          this.getPageData();
          this.footer_partners_text = this.configurations.config.fooer_partners_text.value;
		  this.total_score = (( parseInt(this.configurations.config.tour_guide_points.value) + parseInt(this.configurations.config.quality_price_points.value) + parseInt(this.configurations.config.activities_points.value) )/3).toFixed(2);
		  console.log(this.total_score);
        }
      });
    });
    // this.dataService.getCsrf().subscribe((response: any) => {
    //   console.log(response);
    //   this.bookingService.store({}).subscribe((response: any) => {
    //     console.log(response);
    //   });
    // });
  }

  ngOnChanges(changes: SimpleChanges) {
    if(this.initDone){
      this.getPageData();
    }
  }

  getMonths($event: any, firstLoad = false){
    this.dataService.getMonths($event).subscribe((months: any) => {
      this.months = months;
      this.setMonthOptions();
      this.setComingMonths();
    });
  }

  setAttributeOptions(){
    this.attributeOptions = [];
    this.attributeOptions = this.attributes.map((item: any) => {
      return {
        key: item.id,
        value: item.title
      }
    });
    this.attributeOptions.unshift({
      key: '',
      value: 'Bekijk alle reizen'
    });
  }

  setMonthOptions(){
    this.monthOptions = [];
    this.monthOptions = this.months.map((item: any) => {
      return {
        key: item.value,
        value: item.label + " (" + item.count + ")"
      }
    });
    this.monthOptions.unshift({
      key: '',
      value: 'Bekijk alle data'
    });
  }

  setComingMonths(){
    let currentMonth = Number(formatDate((new Date()), "YYYYMM", "en"));
    let cnt=0;
    this.comingMonths = this.months.filter((item: any) => {
      if(Number(item.value) > currentMonth && cnt < 3){
        cnt++;
        return item;
      }
    });
  }

  getIconImage(icon: string){
    let key = icon.toLowerCase().replace(" ", "_") + "_icon";
    return this.configurations.config[key].value;
  }
  getIconText(icon: string){
    let key = icon.toLowerCase().replace(" ", "_") + "_title";
    return this.configurations.config[key].value;
  }
  selectIcon(){
    this.selectBox = !this.selectBox;
  }
  selectIconYear(){
    this.selectYear = !this.selectYear;
  }
  selectIconMonth(){
    this.selectMonth = !this.selectMonth;
  }
  toggleAgeGroups(){
    this.showAgeGroups = !this.showAgeGroups;
  }
  toggleMonths(i: any){
	  this.showMonths = !this.showMonths;
  }
  tripEndDate(trip: any){
    console.log(trip);
		return (new Date(trip.start_date)).setDate((new Date(trip.start_date)).getDate() + trip.duration-1);
	}

}
