import { Component, Input, OnInit, SimpleChanges, ElementRef  } from '@angular/core';
import { DataService } from '../../services/data.service';
import { TripSliderService } from '../../services/trip-slider.service';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-trip-slider',
  templateUrl: './trip-slider.component.html',
  styleUrls: ['./trip-slider.component.scss']
})
export class TripSliderComponent {
	@Input() trips: any = [];
	skills: any;
	pageLoading = false;
	locations: any;
	parsedData: any; 
	configurations: any;
	slideConfig = { prevArrow: false, arrows: false, nextArrow: false, dots: false, infinite: false, slidesToShow: 3, slidesToScroll: 3, autoplay: false, autoplaySpeed: 3000, responsive: [{ breakpoint: 900, settings: { slidesToShow: 2, slidesToScroll: 1, } }, { breakpoint: 800, settings: { slidesToShow: 2, slidesToScroll: 2, } }, { breakpoint: 600, settings: { slidesToShow: 1, slidesToScroll: 1, centerMode: true, } }] };
	constructor(private dataService: DataService,
		private tripSliderService: TripSliderService,
		private sanitizer: DomSanitizer) {
			

	}

	ngOnInit(){
		this.dataService.configurationsSubject.subscribe((configurations: any) => {
			if(configurations.settings){
			  this.configurations = configurations;
			}
		});
		if(!this.pageLoading){
			this.pageLoading = true;
			this.parsedData = JSON.parse(this.trips); 
			this.tripSliderService.getTripSliderData(this.parsedData).subscribe((response: any) => {
				this.locations = response.trip;
				this.pageLoading = false;
				
			});
		}
		
	}
	getIconImage(icon: string){
		let key = icon.toLowerCase().replace(" ", "_") + "_icon";
		return this.configurations.config[key].value;
	}
	
}
