import { Component, Input, SimpleChanges, ViewChild, ViewContainerRef, ComponentFactoryResolver, Self, Injector } from '@angular/core';
import { DataService } from '../services/data.service';
import { ModalService } from '../services/modal.service';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { createCustomElement } from '@angular/elements';
import { BlogPostComponent } from '../common/blog-post/blog-post.component';


@Component({
  selector: 'app-destination',
  templateUrl: './destination.component.html',
  styleUrls: ['./destination.component.scss']
})
export class DestinationComponent {
  @Input() pageId!: any;
  configurations: any = {};
  page: any = null;
  initDone = false;

  iframeVideo: SafeResourceUrl | undefined;
  
  pageLoading = false;
  
	components: any[] = [
		BlogPostComponent
	];
  
  tab = 1;
  getTabData(tab: any){
	  this.tab = tab;
  }
  
  constructor(
  private dataService: DataService, 
  private modalService: ModalService, 
  private sanitizer: DomSanitizer,
  private injector: Injector) {

  }

  ngOnInit(){
	const element = createCustomElement(BlogPostComponent, { injector: this.injector });
	customElements.define('app-blog-post', element);
    this.dataService.configurationsSubject.subscribe((configurations: any) => {
      if(configurations.settings){
        this.configurations = configurations;
        this.initDone = true;
        this.getPageData();
      }
    });
  }

  totalMinimumTripFee(min_trip_fee: any, nature_disaster_insurance: any, sgr_bijdrage: any){
    const total = Number(min_trip_fee) + Number(nature_disaster_insurance) + Number(sgr_bijdrage);
    return total;
  }

  getPageData(){
    this.pageLoading = true;
    this.dataService.getPageData(this.pageId).subscribe((response: any) => {
      this.page = response;
      this.iframeVideo =  this.sanitizer.bypassSecurityTrustResourceUrl(this.page.pageable.intro_video);
	  this.page.content = this.sanitizer.bypassSecurityTrustHtml(this.page.content);
	  this.pageLoading = false;
      this.dataService.setMeta({
        "title": this.page.meta_title,
        "description": this.page.meta_description,
        "keywords": this.page.meta_keywords,
        "date": this.page.updated_at
      });
    });
  }

  ngOnChanges(changes: SimpleChanges) {
		if(this.initDone){
			this.getPageData();
		}
	}

  openModal(id: string) {
    this.modalService.open(id);
  }

  closeModal(id: string) {
      this.modalService.close(id);
  }
  getIconImage(icon: string){
    let key = icon.toLowerCase().replace(" ", "_") + "_icon";
    return this.configurations.config[key].value;
  }

}
