import { Component, HostListener } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { VakantiesService } from '../services/vakanties.service';
import {DataService} from "../services/data.service";


@Component({
  selector: 'app-vakanties',
  templateUrl: './vakanties.component.html',
  styleUrls: ['./vakanties.component.scss'],


})
export class VakantiesComponent {

	trips: any = [];
	tripTypes: any;
	page:any = null;
	search = new FormControl();
	allVanties = true;
	allFilter = true;
	Vertrekdatum = true;
	configurations: any;
	params: any;
	initDone = false;
	pageLoading = false;
	meta_title: any = {};
	meta_description: any = {};
	total = 0 ;
	loadingMore = false;
	allContentLoaded = false;
	enableInfiniteScroll = true;
	pageNumber = 1;
	filters: any = {
		dates: '',
		europe_only: false,
		all_other: false,
		trip_types: {}
	}

	

  slideConfig = { dots: true, infinite: false, slidesToShow: 1, slidesToScroll: 1, autoplay: false, autoplaySpeed: 3000, responsive: [{ breakpoint: 900, settings: { slidesToShow: 1, slidesToScroll: 1, } }, { breakpoint: 800, settings: { slidesToShow: 1, slidesToScroll: 1, } }, { breakpoint: 600, settings: { slidesToShow: 1, slidesToScroll: 1, centerMode: true, } }] };
	allVantiesIcon(){
		this.allVanties = !this.allVanties;
	}
	allFilterIcon(){
		this.allFilter = !this.allFilter;
	}
	VertrekdatumIcon(){
		this.Vertrekdatum = !this.Vertrekdatum;
	}
	hasSelectedTripTypes(): boolean {
		return Object.values(this.filters.trip_types).some(value => value);
	}	  
	constructor(
		private vakantiesService: VakantiesService,
		private route: ActivatedRoute,
		private dataService: DataService,
		private router: Router,

	) {
	}
	loadMoreContent() {
		if (!this.loadingMore) {
			this.loadingMore = true;
		
			// Increment the page number or load more data as needed.
			this.pageNumber++;
		
			// Fetch more trip data using this.pageNumber or any other logic.
			this.vakantiesService.getVakanties(this.filters, this.pageNumber).subscribe(
			(response: any) => {
				this.trips = this.trips.concat(response.trip.data);
				this.total = response.trip.total;
		
				// Check if all content has been loaded.
				if (this.trips.length >= this.total) {
					this.allContentLoaded = true;
				}
		
				// Reset the loading flag when content is loaded.
				this.loadingMore = false;
			},
			(error: any) => {
				// Handle errors if necessary.
				this.loadingMore = false;
			}
			);
		}
	}

	ngOnInit(){
		this.getTripData();
		this.route.queryParams.subscribe((params: any) => {
		  this.initDone = true;
		  this.params = params;
		  this.dataService.configurationsSubject.subscribe((configurations: any) => {
			if(configurations.settings){
			  this.configurations = configurations;
			//   this.meta_title = this.configurations.config.vakanties_meta_title.value;
			//   this.meta_description = this.configurations.config.vakanties_meta_description.value;
			}
		  });
		});
		this.vakantiesService.getTripTypes().subscribe((response: any) => {
			this.tripTypes = response;

		});
	}
	next: any;
	firstPage(){
		this.pageNumber = 1;
		this.trips = [];
		this.getTripData();
		this.allFilter = true;
	}
	nextPage(){
		this.pageNumber++;
		this.getTripData()
	}
	resetFilters(){
		this.pageNumber = 1;
		this.filters.europe_only = false;
		this.filters.all_other = false;
		this.filters.trip_types = {};
		this.trips = [];
		this.getTripData();
	}
	getTripData(){
		if(!this.pageLoading){
		  this.pageLoading = true;
		  this.vakantiesService.getVakanties(this.filters, this.pageNumber).subscribe((response: any) => {
			
			this.trips = this.trips.concat(response.trip.data);
			this.total = response.trip.total;
			this.page = response.page;
			this.dataService.setMeta({
				"title": this.page.meta_title,
				"description": this.page.meta_description,
				"keywords": '',
				"date": ''
			});
			this.pageLoading = false;
		  });
		}
	}
	getIconImage(icon: string){
		let key = icon.toLowerCase().replace(" ", "_") + "_icon";
		return this.configurations.config[key].value;
	}
	tripEndDate(trip: any){
		return (new Date(trip.start_date)).setDate((new Date(trip.start_date)).getDate() + trip.duration-1);
	}


}
