import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { AuthService } from '../../../services/auth/auth.service';
import {DataService} from "../../../services/data.service";
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-forum-new',
  templateUrl: './forum-new.component.html',
  styleUrls: ['./forum-new.component.scss']
})
export class ForumNewComponent {
	configurations: any;
	pageLoading = false;
	authUser: any;
	newTopicForm:any;
	forum: any;
	isSubmitted = false;
	message:any;
	
	constructor(
		private router: Router,  
		private authService: AuthService,
		private dataService: DataService,
		private route: ActivatedRoute,
		private fb: UntypedFormBuilder
	) {
		this.newTopicForm = this.fb.group({
			forum_category_id: ['', Validators.required],
			title: ['', Validators.required],
			content: ['', Validators.required]
		});
	}
	ngOnInit() {
		this.dataService.configurations.subscribe((configurations: any) => {
		  if(configurations.settings){
			this.configurations = configurations;
			
		  }
		});
	}
}
